import React from "react";
import { notFound } from "next/navigation";
import Link from "next/link";
import { projects } from "../../../data/projects";

export default function ProjectDetailPage({ params }: { params: { slug: string } }) {
  const project = projects.find((p) => p.slug === params.slug);
  if (!project) return notFound();

  return (
    <main className="px-6 sm:px-10 py-10 mx-auto max-w-5xl">
      <nav className="mb-6">
        <Link href="/projects" className="text-sm text-foreground/70 hover:text-foreground">
          ‚Üê Back to Projects
        </Link>
      </nav>

      <header className="mb-6">
        <h1 className="text-3xl sm:text-4xl font-extrabold tracking-tight mb-2">{project.title}</h1>
        <div className="inline-flex items-center px-2 py-1 rounded-full text-xs font-medium border bg-foreground/5 border-foreground/15 text-foreground/80">
          {project.status.replace("-", " ")}
        </div>
      </header>

      {project.imageUrl && (
        <div className="rounded-xl overflow-hidden border border-foreground/10 mb-6 bg-foreground/5">
          <img src={project.imageUrl} alt={`${project.title} cover`} className="w-full h-64 object-cover" />
        </div>
      )}

      <section className="mb-8">
        <h2 className="text-xl font-semibold mb-2">Overview</h2>
        <p className="text-foreground/80 leading-relaxed">{project.details || project.description}</p>
      </section>

      <section className="mb-8">
        <h3 className="text-lg font-semibold mb-2">Technologies</h3>
        <div className="flex flex-wrap gap-2">
          {project.technologies.map((tech) => (
            <span key={tech} className="px-3 py-1 bg-foreground/5 text-foreground/80 rounded-full text-sm">
              {tech}
            </span>
          ))}
        </div>
      </section>

      {project.moreImages && project.moreImages.length > 0 && (
        <section className="mb-10">
          <h3 className="text-lg font-semibold mb-3">Gallery</h3>
          <div className="grid grid-cols-1 sm:grid-cols-2 md:grid-cols-3 gap-4">
            {project.moreImages.map((src, i) => (
              <div key={i} className="rounded-lg overflow-hidden bg-foreground/5 border border-foreground/10">
                <img src={src} alt={`${project.title} ${i + 1}`} className="w-full h-40 object-cover" />
              </div>
            ))}
          </div>
        </section>
      )}

      <div className="flex gap-3">
        {project.demoUrl && (
          <a
            href={project.demoUrl}
            target="_blank"
            rel="noopener noreferrer"
            className="flex-1 sm:flex-none sm:px-6 sm:py-2 px-4 py-2 bg-foreground text-background rounded-lg font-medium text-center hover:bg-foreground/90 transition-colors"
          >
            Live Demo
          </a>
        )}
        {project.githubUrl && (
          <a
            href={project.githubUrl}
            target="_blank"
            rel="noopener noreferrer"
            className="flex-1 sm:flex-none sm:px-6 sm:py-2 px-4 py-2 border border-foreground/20 text-foreground rounded-lg font-medium text-center hover:border-foreground/40 hover:bg-foreground/5 transition-colors"
          >
            GitHub
          </a>
        )}
      </div>
    </main>
  );
}
